import React, { useState } from 'react';
import {
  CssBaseline,
  Container,
  Typography,
  TextField,
  Button,
  Box,
} from '@mui/material';
import axios from 'axios';
import md5 from 'crypto-js/md5';

function App() {
  const [formData, setFormData] = useState({
    departureCity: '',
    destinationCity: '',
    departureDate: '',
    returnDate: '',
    adults: 1,
    children: 0,
    infants: 0,
  });
  const [iataCodes, setIataCodes] = useState({
    departure: '',
    destination: '',
  });
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const getIataCode = async (city) => {
    try {
      const response = await axios.get(
        `https://autocomplete.travelpayouts.com/places2?term=${city}&locale=en&types[]=city`
      );
      if (response.data.length > 0) {
        return response.data[0].code;
      }
      return null;
    } catch (error) {
      console.error(`   IATA   ${city}:`, error);
      return null;
    }
  };

  const generateSignature = (marker, token) => {
    const data = `${marker}:${token}`;
    return md5(data).toString();
  };

  const handleSearch = async (event) => {
    event.preventDefault();
    setLoading(true);

    const departureCode = await getIataCode(formData.departureCity);
    const destinationCode = await getIataCode(formData.destinationCity);

    if (!departureCode || !destinationCode) {
      alert('   IATA-  .');
      setLoading(false);
      return;
    }

    setIataCodes({ departure: departureCode, destination: destinationCode });

    //  
    const marker = '597745';
    const token = '7f9cb932bf7d92dfc7fa0b711c0c932c';
    const signature = generateSignature(marker, token);

    //  IP 
    let userIp = '127.0.0.1'; //  
    try {
      const ipResponse = await axios.get('https://api64.ipify.org?format=json');
      userIp = ipResponse.data.ip;
    } catch (error) {
      console.error('   IP:', error);
    }

    const searchRequestData = {
      signature,
      marker,
      host: window.location.hostname,
      user_ip: userIp,
      locale: 'en',
      trip_class: 'Y',
      passengers: {
        adults: formData.adults,
        children: formData.children,
        infants: formData.infants,
      },
      segments: [
        {
          origin: departureCode,
          destination: destinationCode,
          date: formData.departureDate,
        },
        formData.returnDate && {
          origin: destinationCode,
          destination: departureCode,
          date: formData.returnDate,
        },
      ].filter(Boolean), //  null,   returnDate
    };

    try {
      const searchResponse = await axios.post(
        'https://api.travelpayouts.com/v1/flight_search',
        searchRequestData,
        { headers: { 'Content-Type': 'application/json' } }
      );

      const searchId = searchResponse.data.meta.uuid;
      console.log(' search_id:', searchId);

      //  
      await getSearchResults(searchId);
    } catch (error) {
      console.error('  :', error.response?.data || error.message);
      alert('    .');
    } finally {
      setLoading(false);
    }
  };

  const getSearchResults = async (searchId) => {
    try {
      const resultResponse = await axios.get(
        `https://api.travelpayouts.com/v1/flight_search_results?uuid=${searchId}`
      );

      if (resultResponse.data && resultResponse.data.success) {
        setResults(resultResponse.data.data);
        console.log(' :', resultResponse.data.data);
      } else {
        alert('  .');
      }
    } catch (error) {
      console.error('    :', error);
      alert('    .');
    }
  };

  return (
    <Container>
      <CssBaseline />
      <Typography variant="h4" align="center" gutterBottom>
        Flight Search
      </Typography>
      <Box component="form" onSubmit={handleSearch} sx={{ mt: 4 }}>
        <TextField
          label="Departure City"
          name="departureCity"
          value={formData.departureCity}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          required
        />
        <TextField
          label="Destination City"
          name="destinationCity"
          value={formData.destinationCity}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          required
        />
        <TextField
          label="Departure Date"
          type="date"
          name="departureDate"
          value={formData.departureDate}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          InputLabelProps={{ shrink: true }}
          required
        />
        <TextField
          label="Return Date"
          type="date"
          name="returnDate"
          value={formData.returnDate}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          InputLabelProps={{ shrink: true }}
        />
        <TextField
          label="Adults"
          type="number"
          name="adults"
          value={formData.adults}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          required
        />
        <TextField
          label="Children"
          type="number"
          name="children"
          value={formData.children}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Infants"
          type="number"
          name="infants"
          value={formData.infants}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <Button
          type="submit"
          variant="contained"
          color="primary"
          fullWidth
          size="large"
          sx={{ mt: 2 }}
          disabled={loading}
        >
          {loading ? 'Searching...' : 'Search Flights'}
        </Button>
      </Box>
      <Box sx={{ mt: 4 }}>
        <Typography variant="h6">Search Results:</Typography>
        {results.length > 0 ? (
          results.map((flight, index) => (
            <Box key={index} sx={{ mb: 2 }}>
              <Typography>
                {flight.airline} {flight.flight_number} - ${flight.price}
              </Typography>
              <Typography>Departure: {flight.departure_at}</Typography>
              <Typography>Return: {flight.return_at}</Typography>
            </Box>
          ))
        ) : (
          <Typography>No results found.</Typography>
        )}
      </Box>
    </Container>
  );
}

export default App;
